import React from 'react'

export default function Tab({ animated, type, name }) {
    const time = .3
    return (
        <button style={{ width: '20vw', height: '20vw', borderRadius: '5px', display: 'flex', justifyContent: 'center', alignItems: 'center', animation: `fall ${time}s ease-in 0s`, backgroundColor: 'transparent', borderColor: 'transparent' }}>
            <img src={name} style={{ width: '70%', height: '70%' }} />
        </button>
    )
}
